import React from "react";
import PropTypes from "prop-types";

const objectToArraySelector = (obj) => {
  return Object.keys(obj).map((key) => obj[key]);
};

const EntryList = ({
  entryList,
  onChange,
  onDeleteClick,
  onUpdateClick,
  onSave,
  addEntry = false,
  onClose,
  onNewItemChange,
  saving = false,
  deleting = null,
  updating = null,
}) => {
  const entryArray = objectToArraySelector(entryList);
  return (
    <table className="table">
      <thead>
        <tr>
          <th>Name</th>
          <th />
          <th />
        </tr>
      </thead>
      <tbody>
        {entryArray
          .filter((a) => a)
          .map((entry) => {
            return (
              <tr key={entry.id}>
                <td>
                  <input
                    type="text"
                    name="addEntry"
                    className="form-control"
                    value={entry.value}
                    placeholder="Enter value"
                    onChange={() => onChange(event, entry.id)}
                  />
                </td>
                <td>
                  <button
                    className="btn btn-outline-primary"
                    disabled={addEntry}
                    onClick={() => onUpdateClick(entry.id)}
                  >
                    {updating && updating === entry.id
                      ? "updating..."
                      : "update"}
                  </button>
                </td>
                <td>
                  <button
                    className="btn btn-outline-danger"
                    onClick={() => onDeleteClick(entry)}
                  >
                    {deleting && deleting === entry.id
                      ? "Deleting.."
                      : "Delete"}
                  </button>
                </td>
              </tr>
            );
          })}
        {addEntry && (
          <tr>
            <td>
              <input
                type="text"
                name="addEntry"
                className="form-control"
                placeholder="add entry.."
                onChange={() => onNewItemChange(event, entryArray.length + 1)}
                maxLength="20"
              />
            </td>
            <td>
              <button className="btn btn-primary" onClick={onSave}>
                {saving ? "saving.." : "save"}
              </button>
              <button className="close" aria-label="Close" onClick={onClose}>
                <span aria-hidden="true">&times;</span>
              </button>
            </td>
          </tr>
        )}
      </tbody>
    </table>
  );
};

EntryList.propTypes = {
  entryList: PropTypes.object.isRequired,
  onDeleteClick: PropTypes.func.isRequired,
  addEntry: PropTypes.bool,
  onChange: PropTypes.func.isRequired,
  onSave: PropTypes.func.isRequired,
  onClose: PropTypes.func.isRequired,
  saving: PropTypes.bool,
  deleting: PropTypes.bool,
  updating: PropTypes.number,
  onUpdateClick: PropTypes.func.isRequired,
  onNewItemChange: PropTypes.func.isRequired,
};

export default EntryList;
