import React from "react";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import { bindActionCreators } from "redux";
import * as flightActions from "../../redux/actions/flightActions";
import { newFlight } from "../../../tools/mockData";
import EntryList from "./EntryList";
import Spinner from "../common/Spinner";
import { toast } from "react-toastify";

class EntryPage extends React.Component {
  state = {
    flight: newFlight,
    addEntry: false,
    saving: false,
    deleting: null,
    updating: null,
    entry: {},
  };
  componentDidMount() {
    const { loadFlights, flights, flight } = this.props;
    if (flights.length === 0) {
      loadFlights().catch((error) => alert("Loading flights failed " + error));
    } else {
      this.setState({ flight });
    }
  }

  componentDidUpdate(prevProps) {
    if (this.props.flight !== prevProps.flight) {
      this.setState({ flight: this.props.flight });
    }
  }

  handleClick = () => {
    this.setState({ addEntry: true });
  };

  handleChange = (event, entryId) => {
    const entryName = this.props.isPurchase ? "purchases" : "specialMeals";
    this.setState((prevState) => ({
      ...prevState,
      flight: {
        ...prevState.flight,
        [entryName]: {
          ...prevState.flight[entryName],
          [entryId]: { id: entryId, value: event.target.value },
        },
      },
    }));
  };

  handleNewItemChange = (event, entryId) => {
    this.setState({
      entry: { id: entryId, value: event.target.value },
    });
  };
  handleDelete = (entry) => {
    this.setState({ deleting: entry.id });
    const entryName = this.props.isPurchase ? "purchases" : "specialMeals";
    const tempFlight = {
      ...this.state.flight,
      [entryName]: {
        ...this.state.flight[entryName],
        [entry.id]: null,
      },
    };
    this.props
      .saveFlight(tempFlight)
      .then(() => {
        this.setState({ deleting: null });
        toast.success("Entry deleted Successfully");
      })
      .catch((error) => {
        this.setState({ deleting: null });
        toast.error("Deleting entry failed");
        console.log("deletion failed " + error);
      });
  };

  handleUpdate = (entryId) => {
    this.setState({ updating: entryId });
    this.props
      .saveFlight(this.state.flight)
      .then(() => {
        this.setState({ updating: null });
        toast.success("Entry updated");
      })
      .catch((error) => {
        this.setState({ updating: null });
        toast.error("updating entry failed");
        console.log("updating entry failed " + error);
      });
  };

  handleSave = () => {
    this.setState({ saving: true });
    const entryName = this.props.isPurchase ? "purchases" : "specialMeals";
    const temp = {
      ...this.state.flight,
      [entryName]: {
        ...this.state.flight[entryName],
        [this.state.entry.id]: {
          id: this.state.entry.id,
          value: this.state.entry.value,
        },
      },
    };
    this.props
      .saveFlight(temp)
      .then(() => {
        this.setState({ saving: false });
        this.setState({ addEntry: false });
        toast.success("New entry added");
      })
      .catch((error) => {
        this.setState({ saving: false });
        this.setState({ addEntry: false });
        toast.error("Adding new entry failed");
        console.log("adding entry failed " + error);
      });
  };

  handleClose = () => {
    this.setState({ addEntry: false });
  };
  render() {
    return (
      <>
        <h2>
          {this.props.isPurchase ? "In-Flight Purchases" : "Special Meals"}
        </h2>
        {this.props.flights.length === 0 ? (
          <Spinner />
        ) : (
          <>
            <button
              style={{ marginTop: 10, marginBottom: 20, float: "left" }}
              className="btn btn-primary"
              onClick={this.handleClick}
            >
              Add Item
            </button>
            <EntryList
              entryList={
                this.props.isPurchase
                  ? this.state.flight.purchases
                  : this.state.flight.specialMeals
              }
              onDeleteClick={this.handleDelete}
              onUpdateClick={this.handleUpdate}
              updating={this.state.updating}
              addEntry={this.state.addEntry}
              onChange={this.handleChange}
              onNewItemChange={this.handleNewItemChange}
              onSave={this.handleSave}
              onClose={this.handleClose}
              saving={this.state.saving}
              deleting={this.state.deleting}
            ></EntryList>
          </>
        )}
      </>
    );
  }
}

EntryPage.propTypes = {
  flights: PropTypes.array.isRequired,
  // passengers: PropTypes.array.isRequired,
  loadFlights: PropTypes.func.isRequired,
  saveFlight: PropTypes.func.isRequired,
  flight: PropTypes.object.isRequired,
  isPurchase: PropTypes.bool.isRequired,
  // loadPassengers: PropTypes.func.isRequired,
};

function getFlightByName(flights, name) {
  return flights.find((a) => a.name === name) || newFlight;
}

function mapStateToProps(state, ownProps) {
  const flightName = ownProps.match.params.name;
  const isPurchase = ownProps.match.path === "/admin/purchases/:name";
  const flight =
    state.flights.length > 0
      ? getFlightByName(state.flights, flightName)
      : newFlight;
  return {
    flight,
    isPurchase,
    flights: state.flights,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    loadFlights: bindActionCreators(flightActions.loadFlights, dispatch),
    saveFlight: bindActionCreators(flightActions.saveFlight, dispatch),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(EntryPage);
